events.on('ready', function () {
	var config = navigate.currentScreen.model;
	scorm.saveObject('language', config.language);

	ajaxRequest('../../data/texts.json', 'texts-translate');
	ajaxRequest('../../data/images.json', 'images-translate');
	ajaxRequest('../../data/videos.json', 'videos-translate');
	ajaxRequest('../../data/audios.json', 'audios-translate');
	ajaxRequest('../../data/links.json', 'links-translate');

});

function ajaxRequest(json, data) {
	$.getJSON(json, function (arrayLanguages) {
		if(data == "texts-translate") {
			$('*').each(function () {
				const getlanguage = scorm.loadObject('language');
				const getParameterLanguage = $(this).data(data);
				$(this).html(arrayLanguages[0][getlanguage][getParameterLanguage]);
			});
		} else {
			$(`[data-${data}]`).each(function(){
				const getlanguage = scorm.loadObject('language');
				const getParameterLanguage = $(this).data(data);

				const src = data === "links-translate" ? "href" : "src";
				$(this).attr(src, arrayLanguages[0][getlanguage][getParameterLanguage][src])
				$(this).attr("class", arrayLanguages[0][getlanguage][getParameterLanguage].class)
				$(this).attr("aria-label", arrayLanguages[0][getlanguage][getParameterLanguage].ariaLabel)
			});
		}
	});
}
